.main-straight {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;


  .main-blocks {
    box-sizing: border-box;
    display: flex;
    justify-content: flex-start;
    flex-direction: column;
    align-items: flex-start;
    gap: 80px;
    width: 1440px;
    min-height: 1957px;
    height: auto;
    padding: 41px 70px;
    background: url("/img/bgndForPage1.svg"), white;

    .main-block1 {
      display: flex;
      flex-direction: column;
      gap: 40px;

      .path {
        display: flex;
        flex-direction: row;
        gap: 10px;
        align-items: center;

        .p6 {
          color: #858585;

        }

        .block1_block {
          display: flex;
          width: 5px;
          height: 5px;
          background: #FF1A1A;
          transform: rotate(45deg);
        }
      }

      .main-block1-blocks {
        display: flex;
        flex-direction: column;
        gap: 20px;

        h1,
        .p2 {
          color: #1D1E33;
          text-decoration: none;
        }

        .p1 {
          width: 1106px;
          height: 75px;
        }

        .blocks {
          display: flex;
          flex-direction: row;
          gap: 20px;
          align-items: center;

          .border {
            display: flex;
            width: 1px;
            height: 15px;
            background: #1D1E33;
          }

          .p2 {
            border-bottom: 1px solid #1D1E33;
          }
        }
      }
    }

    .main-block2 {
      display: flex;
      flex-direction: row;
      gap: 32px;

      .block2-conts {
        display: flex;
        flex-direction: column;
        gap: 27px;

        .block2-cont1 {
          display: flex;
          flex-direction: column;
          gap: 27px;
        }

        .block2-cont2 {
          display: flex;
          flex-direction: column;
          gap: 20px;
          align-items: flex-end;
          width: 100%;

          .border {
            width: 958px;
            height: 1px;
            background: #858585;
          }

          .name {
            display: flex;
            flex-direction: column;


            gap: 10px;

            h6 {
              color: #1D1E33;
            }
          }
        }
      }
    }

    .main-block3 {
      h4 {
        text-transform: none;

        a {
          color: #004CA6;
        }
      }
    }

    .main-block4 {
      margin-bottom: 65px;
      display: flex;
      flex-direction: row;
      gap: 49px;
      .block4-conts1 {
        display: flex;
        flex-direction: column;
        gap: 40px;

        .conts1-cont1 {
          display: flex;
          flex-direction: column;
          gap: 15px;
        }

        .conts1-cont2 {
          display: flex;
          flex-direction: column;
          gap: 25px;

          .file {
            display: flex;
            flex-direction: column;
            gap: 10px;

            .file_body {
              width: 750px;
              height: 60px;
              display: flex;
              flex-direction: row;
              justify-content: space-between;
              gap: 10px;
              border: 1px solid #1D1E33;
              align-items: center;
              padding: 0 30px 0 30px;
              box-sizing: border-box;

              .l {
                color: #858585;
              }

              .img {
                cursor: pointer;
              }

              input[type="file"] {
                display: none;
              }

              span {
                font-weight: 400;
                font-size: 18px;
                line-height: 140%;
                letter-spacing: 0.06em;
                font-family: $gilroyRegular;
              }
            }
          }

          .textarea {
            display: flex;
            flex-direction: column;
            gap: 10px;

            .textarea_body {
              box-sizing: border-box;
              display: flex;
              min-width: 750px;
              max-width: 750px;
              min-height: 200px;
              height: auto;
              padding: 30px;
              border: 1px solid #1D1E33;
              padding: 18px 30px;
              outline: none;
            }

            .textarea_body::placeholder,
            .textarea_body {
              font-weight: 400;
              font-size: 18px;
              line-height: 140%;
              letter-spacing: 0.06em;
              font-family: $gilroyRegular;

            }

            .p1 {
              display: flex;
              flex-direction: row;
              align-items: center;
            }
          }

          .input {
            width: 750px;
            height: 95px;
            display: flex;
            flex-direction: column;
            gap: 10px;

            .input_body {
              box-sizing: border-box;
              width: 750px;
              height: 60px;
              border: 1px solid #1D1E33;
              display: flex;
              padding-left: 30px;
              outline: none;
            }

            .input_body::placeholder,
            .input_body {
              font-weight: 400;
              font-size: 18px;
              line-height: 140%;
              letter-spacing: 0.06em;
              font-family: $gilroyRegular;

            }

            .p1 {
              display: flex;
              flex-direction: row;
              align-items: center;
            }
          }

          .dang .p4 {
            display: flex;
            flex-direction: row;
            align-items: center;

          }

          .checkbox {
            display: flex;
            flex-direction: row;
            gap: 15px;
            align-items: center;

            .checkbox_cont {
              position: relative;
              width: 24px;
              height: 24px;
              display: flex;

              input[type="checkbox"] {
                opacity: 0;
              }

              .checkbox_body {
                box-sizing: border-box;
                position: absolute;
                display: flex;
                justify-content: center;
                align-items: center;
                width: 24px;
                height: 24px;
                border: 1px solid #1D1E33;
                cursor: pointer;

                .check.active {
                  opacity: 1;
                }

                .check {
                  transition: all 0.5s ease-in-out;
                  opacity: 0;
                  display: flex;
                  width: 12px;
                  height: 12px;
                  background: #1D1E33;
                }
              }
            }
          }
        }
      }

      .border {
        display: flex;
        width: 1px;
        height: 708px;
        background: #858585;
      }
      .block4-conts2{
        display: flex;
        flex-direction: column;
        gap:40px;
        h4{
          text-transform: none;
        }
        .cont1-body{
          display: flex;
          flex-direction: column;
          gap:25px;
          a{
            display: flex;
            flex-direction: row;
            align-items: center;
            gap:15px;
            .p1{
              color:#232323;
            }
          }
        }
      }
    }



  }
}