.main-projects {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;


    .main-blocks {
        box-sizing: border-box;
        display: flex;
        justify-content: flex-start;
        flex-direction: column;
        align-items: flex-start;
        gap: 80px;
        width: 1440px;
        height: 1210px;
        padding: 41px 70px;
        background: url("/img/bgndForPage1.svg"), white;

        .main-block1 {
            display: flex;
            flex-direction: column;
            gap: 40px;

            .path {
                display: flex;
                flex-direction: row;
                gap: 10px;
                align-items: center;

                .p1 {
                    color: #858585;

                }

                .block1_block {
                    display: flex;
                    width: 5px;
                    height: 5px;
                    background: #FF1A1A;
                    transform: rotate(45deg);
                }
            }

            .main-block1-blocks {
                display: flex;
                flex-direction: column;
                gap: 20px;

                h1,
                .p2 {
                    color: #1D1E33;
                    text-decoration: none;
                }

                .p1 {
                    width: 1106px;
                    height: 75px;
                }

                .blocks {
                    display: flex;
                    flex-direction: row;
                    gap: 20px;
                    align-items: center;

                    .border {
                        display: flex;
                        width: 1px;
                        height: 15px;
                        background: #1D1E33;
                    }

                    .p2 {
                        border-bottom: 1px solid #1D1E33;
                    }
                }
            }
        }

        .main-block2 {
            display: flex;
            flex-direction: row;
            flex-wrap: wrap;
            gap: 20px;

            .block2-cont1:hover {
                transition: all .5s ease-in-out;
                background: #1D1E33 !important;
                color: #F9F9F9 !important;

                .cont1-text {
                    svg {
                        transition: all .7s ease-in-out;
                        opacity: 1;
                        visibility:visible;
                    }
                }
            }

            .block2-cont1 {
                cursor: pointer;
                border: 1px solid #1D1E33;
                display: flex;
                flex-direction: column;
                align-items: flex-start;
                box-sizing: border-box;
                padding: 30px 0 0 30px;
                gap: 15px;
                color: #1D1E33;
                justify-content: flex-start;
                width: 420px;
                height: 240px;

                .p4 {
                    width: 347px;
                    height: 88px;
                }

                .cont1-text {
                    display: flex;
                    flex-direction: row;
                    justify-content: space-between;
                    width: 347px;
                    svg{
                        opacity: 0;
                        visibility: hidden;
                    }
                    h5{
                    white-space: nowrap;
                    }
                }

            }

        }

    }
}